import {
  BasicHttpClient,
  HttpClient,
  PageContext,
  ServiceScope } from '@microsoft/sp-client-base';

import IWebPartHost from '../../../local/common/webParts/IWebPartHost';

export class MockHost implements IWebPartHost {
  public pageContext: PageContext;
  public basicHttpClient: BasicHttpClient;
  public httpClient: HttpClient;
  public serviceScope: ServiceScope;

  constructor() {
    this.pageContext = undefined;
    this.basicHttpClient = undefined;
    this.httpClient = undefined;
    this.serviceScope = undefined;
  }
}

/* tslint:disable:export-name */
export default new MockHost();
/* tslint:enable:export-name */
